.in{
  margin:0 auto;
  position:relative;
  top:50%;
  transform:translateY(-50%);
  display: inline-block;
  height: 25px;
  width: 80%;
  /* float: left; */
  background-color:rgb(255,78,34);
  border: 1px solid rgb(255,78,34);
  border-radius: 5px 5px 5px 5px;
  text-align: center;
  line-height: 25px;
  color: rgba(235, 228, 228, 0.753);
}
#a>.am-wingblank{
